import React, { Component } from 'react'
import { CSSTransitionGroup } from 'react-transition-group/'

//Scripts
import { addClass, transition } from './script'

//Components
import Hero from './components/hero/hero'
import About from './components/about/about'
import BackgroundImage from './components/bg-image/bg-image'
import Situation from './components/situation/situation'
import Residence from './components/residence/residence'
import Avantages from './components/avantages/avantages'
import CTA from './components/cta/cta'

const h1 = document.getElementsByTagName('H1')
const title = document.getElementsByClassName('title')

class App extends Component {

   componentDidMount() {
      addClass(h1, 'anim slide-up')
      window.onscroll = e => {
         transition(title, 'slide-up')
         transition(document.querySelectorAll('.para'), 'fade-up')
      }
   }

   render(){
      const transitionParams = {
            transitionName: 'fade',
            transitionAppear: true,
            transitionAppearTimeout: 2000,
            transitionEnter: true,
            transitionEnterTimeout: 500,
            transitionLeave: true,
            transitionLeaveTimeout: 500
      }

      return(
         <CSSTransitionGroup {...transitionParams}>
            <div className="app-container">
               <Hero />
               <About />
               <BackgroundImage bgImageUrl='./img/transition1.jpg'/>
               <Situation />
               <Residence />
               <BackgroundImage bgImageUrl='./img/transition2.jpg'/>
               <Avantages />
               <CTA />
            </div>
         </CSSTransitionGroup>
      )
   }
}

export default App



