import React, { Component } from 'react'

//Components
import SituationSlider from './slider'

class Situation extends Component {

   render(){
      return(
         <section className="situation-section col-100">
               <div className="content col-100">
                  <div className="text col-50">
                     <div className="text-wrapper">
                        <div className="title">
                           <div className="top"><h1>Situation</h1></div>
                           <div className="bottom"><h1>géographique</h1></div>
                        </div>
                        <div className="para">
                           <ul>
                              <li className="anim fade-up" style={{ transitionDelay: '200ms' }}>
                                 20 minutes de la gare TGV et du centre commercial de la
                                 Part-Dieu en transports en commun.
                              </li>
                              <li className="anim fade-up" style={{ transitionDelay: '300ms' }}>
                                 Accessibilité Nombreuses lignes de bus, métro A, tramway T3
                                 et navette aéroport Rhônexpress depuis le pôle multimodal.
                              </li>
                              <li className="anim fade-up" style={{ transitionDelay: '400ms' }}>
                                 2e ville du Rhône Une croissance démographique de près de
                                 5% depuis 5 ans.
                              </li>
                           </ul>
                        </div>
                     </div>
                  </div>

                  <div className="image col-50">
                     <SituationSlider />
                  </div>
               </div>
         </section>
      )
   }
}

export default Situation



