export function addClass(elem, className) {
   for(let i=0; i <= elem.length - 1; i++) {
      elem[i].className += className
   }
}

export function transition(parentElem, type) {
      if(parentElem.length && parentElem.length > 0) {
         let scrollTop = window.scrollY
         let scrollToAnim = scrollTop + (window.innerHeight * 0.75)

         for(let i=0; i <= parentElem.length - 1; i++) {
            let animElem = parentElem[i].getElementsByClassName('anim')

            if(scrollToAnim >= parentElem[i].offsetTop) {
               for(let j=0; j <= animElem.length - 1; j++) {
                  animElem[j].className = type + ' anim active'
               }
            }else{
               for(let j=0; j <= animElem.length - 1; j++) {
                  animElem[j].className = type + ' anim'
               }
            }
         }
      }
}



