import React, { Component } from 'react'

//Components
import Button from './../ui/button'

//Styles
import 'react-id-swiper/src/styles/css/swiper.css'

//Scripts
import Swiper from 'react-id-swiper'

class HeroSlider extends Component {

   constructor(props){
      super(props)
      this.state = {
         slideImages: [
            './img/hero1.jpg',
            './img/hero2.jpg',
            './img/hero3.jpg'
         ]
      }
   }

   showSlideImage = () => {
      return(
            this.state.slideImages.map( (item, i) => {
               return(
                  <div key={i}>
                     <div className="slide-img col-100" style={{ backgroundImage: `url(${item})` }}></div>
                  </div>
               )
            } )
      )
   }

   render(){
      const swiperParams = {
         effect: 'fade',
         autoplay: true,
         speed: 1800,
         loop: true,
         simulateTouch: false,
         navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
         },
         renderPrevButton: () => <Button className="swiper-button-prev" />,
         renderNextButton: () => <Button className="swiper-button-next" />,
      }

      return(
         <div className="slider-container col-100">
            <Swiper {...swiperParams}>
               {this.showSlideImage()}
            </Swiper>
         </div>
      )
   }
}



export default HeroSlider



