import React, { Component } from 'react'

//Components

class Residence extends Component {

   render(){
      return(
         <section className="residence-section col-100">
               <div className="content col-100">
                  <div className="primary">
                     <div className="bg-image col-100" style={{ backgroundImage: `url(./img/residence1.jpg)` }}></div>
                     <div className="text">
                        <div className="title">
                           <div className="top"><h1>Des résidences</h1></div>
                           <div className="bottom"><h1>de qualité</h1></div>
                        </div>
                        <p>
                           AFRICADY PROMOTION IMMOBILIÈRE vous propose des
                           résidences immobilières de 5 pièces. Ouverts sur d’agréables
                           espaces extérieurs, toutes offrent des pièces de vie
                           chaleureuses imaginées comme de véritables espaces à vivre
                        </p>
                     </div>
                  </div>
                  <div className="secondary">
                     <div className="text">
                        <ul>
                           <li>
                              Les menuiseries mixtes en bois et aluminium vernis
                              permettent d’associer la noblesse du bois pour les
                              portes, la pérennité de l’aluminium pour le balcon
                              et le portail
                           </li>
                           <li>
                              La présence de TUILE au toit apporte confort,
                              esthétique, sécurité et facilité d’entretien
                           </li>
                           <li>
                              Vivez l’esprit libre et serein au quotidien, grâce à la
                              sécurité d’un portail en acier anti-effraction
                           </li>
                           <li>
                              La présence de Marbre et de carreaux lustrés offre
                              un cadre luxueux où vous pourrez pleinement vous
                              épanouir
                           </li>
                        </ul>
                     </div>
                     <div className="image col-100">
                        <div className="bg-image col-100" style={{ backgroundImage: `url(./img/residence2.jpg)` }}></div>
                     </div>
                  </div>
               </div>
         </section>
      )
   }
}

export default Residence



