import React, { Component } from 'react'

//Components
import HeroSlider from './slider'

class Hero extends Component {

   render(){
      return(
         <section className="hero-section col-100">
               <div className="logo">
                  <a href="./">
                     <img src="./img/logo.png" alt="africady" width="110px" />
                  </a>
               </div>
               <div className="description">
                  <span>Agence Immobilière africady</span>
               </div>

               <div className="scroll">
                  <button onClick={() => document.scrollingElement.scrollTo({top: window.innerHeight, behavior: 'smooth'})}>Faire défiler <i className="arrow"></i></button>
               </div>

               <HeroSlider />
         </section>
      )
   }
}

export default Hero



