import React, { Component } from 'react'

//Components

class About extends Component {

   render(){
      return(
         <section className="about-section col-100">
               <div className="content col-100">
                  <div className="text col-50">
                     <div className="text-wrapper">
                        <div className="title">
                           <div className="top"><h1>A propos</h1></div>
                           <div className="bottom"><h1>de l'entreprise</h1></div>
                        </div>
                        <div className="para">
                           <p className="anim fade-up" style={{ transitionDelay: '200ms' }}>
                              À première vue, Africady joue la carte de la sobriété avec une
                              architecture qui traduit une volonté d’intégration harmonieuse.
                              En effet, la résidence KADY 1 est coiffée d’une toiture dont la
                              pente ménage les différences de niveaux.
                           </p>
                           <p className="anim fade-up" style={{ transitionDelay: '300ms' }}>
                              La lecture architecturale révèle ensuite une évidente recherche
                              d’esthétique. Un jeu de matières et de couleurs souligne la
                              structure de la résidence qui se scinde en deux etages distincts.
                              Elle possède une large clôture ‘’GRIS SOURIS’’ fermer par un
                              portail ‘’Rouge bordeau’’ moderne.
                           </p>
                        </div>
                     </div>
                  </div>

                  <div className="image col-50">
                     <div className="bg-image col-100" style={{ backgroundImage: `url(./img/apropos.jpg)` }}></div>
                  </div>
               </div>
         </section>
      )
   }
}

export default About



