import React, { Component } from 'react'

//Components

class Avantages extends Component {

   render(){
      return(
         <section className="avantages-section col-100">
               <div className="content col-100">
                  <div className="text col-50">
                     <div className="text-wrapper">
                        <div className="title">
                           <div className="top"><h1>Avantages</h1></div>
                           <div className="bottom"><h1>des résidences</h1></div>
                        </div>
                        <div className="para">
                           <p className="anim fade-up" style={{ transitionDelay: '200ms' }}>
                              Vivre en maison ou en appartement relève souvent d’un choix
                              stratégique de localisation. D’un côté le confort de la maison
                              et de ses espaces extérieurs, de l’autre les avantages de la ville.
                              Avec les résidences d’AFRICADY, plus d’inquiétudes, conciliez
                              l’opportunité unique de vivre en maison au cœur de la ville.
                           </p>
                        </div>
                     </div>
                  </div>

                  <div className="list col-100">
                     <div className="item col-third">
                        <div className="bg-image" style={{ backgroundImage: `url(./img/avantages1.jpg)` }}></div>
                        <div className="overlay"></div>
                        <h6>Des surfaces généreuses et bien agencées</h6>
                     </div>
                     <div className="item col-third">
                        <div className="bg-image" style={{ backgroundImage: `url(./img/avantages2.jpg)` }}></div>
                        <div className="overlay"></div>
                        <h6>Jardin privatif bien dimensionné qui prolonge les pièces de vie</h6>
                     </div>
                     <div className="item col-third">
                        <div className="bg-image" style={{ backgroundImage: `url(./img/avantages3.jpg)` }}></div>
                        <div className="overlay"></div>
                        <h6>Présence d’une terrasse pour profiter de la vie en extérieur</h6>
                     </div>
                     <div className="item col-third">
                        <div className="bg-image" style={{ backgroundImage: `url(./img/avantages4.jpg)` }}></div>
                        <div className="overlay"></div>
                        <h6>Une intimité préservée grâce à la présence d’une clôture en béton renforcé</h6>
                     </div>
                     <div className="item col-third">
                        <div className="bg-image" style={{ backgroundImage: `url(./img/avantages5.jpg)` }}></div>
                        <div className="overlay"></div>
                        <h6>Le cadre convivial et la fierté d’avoir sa propre maison</h6>
                     </div>
                  </div>
               </div>
         </section>
      )
   }
}

export default Avantages



