import React, { Component } from 'react'

class BackgroundImage extends Component {

   render(){
      const bgImageUrl = this.props.bgImageUrl ? this.props.bgImageUrl : ''
      return(
         <section className="bg-section col-100">
            <div className="bg-image" style={{ backgroundImage: `url(${bgImageUrl})` }}></div>
         </section>
      )
   }
}

export default BackgroundImage



